<template>
  <div  class="card-block"> 
    <div  class="form-group row" v-for="(itm,index) in selectedQue.questions " :key="itm.id" >
        <label  class="col-sm-12 col-form-label">{{itm.number}} {{convertToText(itm.text) }} ( {{viewType(itm.kind)}} )</label>

        <div  style="right: 10%;" class="col-sm-12" v-for="(answ,index1) in itm.answers " :key="answ.id" >
            {{index1}} , {{answ.text}}
        </div>
    </div> 

  </div>
</template>

<script>
export default {
  name:"appViewQuestion",   
      data:function()
      {
        return {
          types:[{"name":"singleLine","title":"    "},{"name":"radio","title":" "},{"name":"multiLine","title":"    "},{"name":"check","title":"  "},{"name":"decimal","title":""},{"name":"int","title":""},{"name":"linearScale","title":""},{"name":"email","title":""},{"name":"range","title":"   "},{"name":"link","title":" /   "},{"name":"priority","title":" "},{"name":"page","title":"   "},
          {"name":"file","title":"   "},
          {"name":"phone","title":" "},
          
          {"name":"dateTime","title":" "}],

        }
      },
      
    
    methods: { 
      getElementByRef:function(el){
        return this.$refs[el]
      },
      getMyParent:function(obj){return this.$parent},
      setState:function(obj){
        var al={"selectedQue":true}
        if(obj)
          for(var a in obj)
          {
            //if(al[a])
            //  continue
            try{
              this[a]=obj[a] 
            }catch(exp){}
          }
          
      },
      convertToText:function (data){
      return $(data).text()
    },
        viewType:function (type){
      for(var a of this.types)
      {
        if(a.name==type)
          return a.title
      }
      return ""
    },
        
    },
    
    mounted:function (){  
    },

    props:{
        'selectedQue':{default:""},
        
      }, 
   
}
</script>